const uni = require('uni').default;











































export default {
	data() {
		return {
			login:false,
			avatarUrl:"../../static/logo.png",
			uerInfo:{}
		}
	},
	methods: {
		goLogin() {
			if(!this.login){
				uni.navigateTo({
					url:"../login/login"
				})
			}
		}
	}
}



